import React, { useEffect, useState } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import { fetchChartData } from '../services/stockAPI';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const StockChart = ({ symbol, timeframe = '1D' }) => {
  const [chartData, setChartData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const loadChartData = async () => {
      try {
        setLoading(true);
        setError(null);
        const data = await fetchChartData(symbol, timeframe);
        setChartData(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    if (symbol) {
      loadChartData();
    }
  }, [symbol, timeframe]);

  // Auto-refresh every 5 seconds
  useEffect(() => {
    const interval = setInterval(async () => {
      if (symbol && !loading) {
        try {
          const data = await fetchChartData(symbol, timeframe);
          setChartData(data);
        } catch (err) {
          console.error('Error refreshing chart data:', err);
        }
      }
    }, 5000);

    return () => clearInterval(interval);
  }, [symbol, timeframe, loading]);

  if (loading) {
    return (
      <div className="chart-loading">
        <div className="loading-spinner"></div>
        <span>Loading chart data...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="chart-error">
        <span>Error loading chart: {error}</span>
      </div>
    );
  }

  if (!chartData || chartData.length === 0) {
    return (
      <div className="chart-empty">
        <span>No chart data available</span>
      </div>
    );
  }

  const data = {
    labels: chartData.map(point => point.time),
    datasets: [
      {
        label: `${symbol} Price`,
        data: chartData.map(point => parseFloat(point.price)),
        borderColor: '#64b5f6',
        backgroundColor: 'rgba(100, 181, 246, 0.1)',
        borderWidth: 2,
        fill: true,
        tension: 0.4,
        pointRadius: 0,
        pointHoverRadius: 6,
        pointHoverBackgroundColor: '#64b5f6',
        pointHoverBorderColor: '#fff',
        pointHoverBorderWidth: 2
      }
    ]
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        mode: 'index',
        intersect: false,
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        titleColor: '#fff',
        bodyColor: '#fff',
        borderColor: '#64b5f6',
        borderWidth: 1,
        callbacks: {
          label: function(context) {
            return `${symbol}: $${context.parsed.y.toFixed(2)}`;
          }
        }
      }
    },
    scales: {
      x: {
        display: true,
        grid: {
          display: false
        },
        ticks: {
          color: '#b8c5d1',
          maxTicksLimit: 8
        }
      },
      y: {
        display: true,
        grid: {
          color: 'rgba(184, 197, 209, 0.1)'
        },
        ticks: {
          color: '#b8c5d1',
          callback: function(value) {
            return '$' + value.toFixed(2);
          }
        }
      }
    },
    interaction: {
      mode: 'nearest',
      axis: 'x',
      intersect: false
    }
  };

  return (
    <div className="stock-chart">
      <div className="chart-header">
        <h3>{symbol} Price Chart</h3>
        <div className="chart-controls">
          <button 
            className={`timeframe-btn ${timeframe === '1D' ? 'active' : ''}`}
            onClick={() => window.location.href = `?timeframe=1D`}
          >
            1D
          </button>
          <button 
            className={`timeframe-btn ${timeframe === '1W' ? 'active' : ''}`}
            onClick={() => window.location.href = `?timeframe=1W`}
          >
            1W
          </button>
          <button 
            className={`timeframe-btn ${timeframe === '1M' ? 'active' : ''}`}
            onClick={() => window.location.href = `?timeframe=1M`}
          >
            1M
          </button>
        </div>
      </div>
      <div className="chart-container">
        <Line data={data} options={options} />
      </div>
      <div className="chart-footer">
        <span className="last-updated">
          Last updated: {new Date().toLocaleTimeString()}
        </span>
        <span className="auto-refresh">
          Auto-refresh: 5s
        </span>
      </div>
    </div>
  );
};

export default StockChart; 